import React, { Component } from 'react'
import { Container } from 'react-bootstrap'
import './css/download.css'
import GameData from './GameData'
import { Table } from 'react-bootstrap'


export class Download extends Component {
    constructor(props) {
        super(props)
        this.state = { txt: props.txt }
    }
    static getDerivedStateFromProps(props, state) {
        if (props.txt !== state.txt) {
            return { txt: props.txt }
        } else {
            return null
        }
    }
    render() {
        let gameDownloadList = Object.keys(GameData).map(game =>
                <tr key={GameData[game].groupid}>
                    <td>
                    <img className="gameTableImg" alt="" 
                        src={`${process.env.PUBLIC_URL}/logos/` + GameData[game].groupid + `_pelilogo.png`} />
                    </td>
                    <td>
                        {GameData[game].gameName}
                    </td>
                    <td>
                        <a href={GameData[game].downloadUrl}>Download</a>
                    </td>
                </tr>
            )
        return(
        <Container fluid className="download">
            <h1>{this.state.txt.download.pageTitle}</h1>
            <p>{this.state.txt.download.infoText}</p>
            <Table striped bordered hover className="table">
                <thead>
                    <tr>
                        <th width="18%">Logo</th>
                        <th width="57%">Peli</th>
                        <th width="25%">Download link</th>
                    </tr>
                </thead>
                <tbody>
                    {gameDownloadList}
                </tbody>
            </Table>
        </Container>
        )
    }
}